.content .title{font-size:16px;font-weight:600;color:#333;padding:0;margin:0 0 16px 0}.content .option{margin:0 0 8px 0}.content .option-container{display:flex;padding:0;margin:0;flex-direction:column;justify-content:flex-start}.content .option-container .colortext{color:#888;border-bottom:1px solid rgba(0,0,0,0);transition:all .3s ease;cursor:pointer}.content .option-container .colortext.active,.content .option-container .colortext:hover{color:#7685e6;border-color:#7685e6}.content .color-icon{content:"";font-family:"Font Awesome 5 Free";font-weight:900;font-size:12px;margin-right:8px}.content .color-black{color:#000}.content .color-blue{color:blue}.content .color-gray{color:gray}.content .color-green{color:green}.content .color-red{color:red}.content .color-white{color:#fff}.content .catalog-button-container{display:grid;grid-template-columns:1fr 1fr}.content .catalog-button-container .catalog-tag-btn{color:#888;font-size:14px;padding:5px 10px;margin:0 5px 5px 0;border:1px solid #888;text-align:center;border-radius:20px;background:rgba(0,0,0,0);transition:all .3s ease;cursor:pointer}.content .catalog-button-container .catalog-tag-btn:hover{color:#7685e6;border-color:#7685e6}@media only screen and (min-width: 601px)and (max-width: 1024px){.content .catalog-button-container{display:flex;flex-direction:column}.content .catalog-button-container .catalog-tag-btn{padding:5px 10px;margin:0 0 7px 0}}@media only screen and (max-width: 600px){.content .title{margin:16px 0}.content .catalog-button-container{display:flex;flex-direction:row}.content .catalog-button-container .catalog-tag-btn{padding:5px 10px;margin:0 7px 0 0}}